html {
  background-color: #edf3f4;
  font-family: Arial;
  width: 100%; }

body {
  width: 100%;
  text-align: center;
  position: relative;
  min-height: 100vh; }

header {
  background-color: #3470c9;
  height: 50px;
  width: 100%;
  font-family: "Arial Bold", Arial;
  color: white;
  position: relative;
  text-align: center; }
  header .header {
    display: inline-block;
    height: 100%;
    width: 100%;
    max-width: 1000px;
    position: relative; }
  header .left {
    position: absolute;
    font-size: 2em;
    color: white;
    text-decoration: none;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
    font-weight: 600; }
  header .right {
    font-weight: 600;
    margin-left: auto;
    position: absolute;
    right: 0px;
    top: 50%;
    transform: translateY(-50%); }
    header .right a {
      padding-right: 10px;
      font-family: Arial;
      color: white;
      text-decoration: none;
      font-size: 1.5em;
      position: relative;
      opacity: 0.7;
      transition: all .1s; }
      header .right a:hover {
        opacity: 1; }
  header .splitter {
    font-size: 2em;
    position: relative;
    top: 5px; }

footer {
  font-family: Arial;
  background-color: #3470c9;
  position: absolute;
  left: 0px;
  bottom: 5px;
  height: 50px;
  width: 100%;
  text-align: center;
  color: white; }
  footer .footer {
    position: relative;
    display: inline-block;
    height: 100%;
    width: 100%;
    max-width: 1000px; }
    footer .footer .footer__text {
      font-size: .7em; }
  footer a {
    color: white;
    text-decoration: none;
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%); }

.blogbody__navigation .navigation__item {
  position: relative;
  display: inline-block;
  color: white;
  height: 20px;
  width: 20px;
  font-size: 2em;
  padding: 10px;
  vertical-align: top;
  background-color: #3470c9; }
  .blogbody__navigation .navigation__item .item__content {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%); }

.blogbody {
  margin-bottom: 70px;
  margin-top: 30px;
  display: inline-block;
  width: 100%;
  max-width: 1000px;
  text-align: center; }
  .blogbody .blogbody__posts {
    display: inline-block;
    width: 100%; }

.post {
  margin-bottom: 10px;
  text-align: left;
  width: 95%;
  display: inline-block; }
  .post .post__title {
    color: #3470c9;
    font-size: 2em;
    font-weight: 600;
    display: inline-block;
    text-decoration: none; }
  .post .post__date {
    font-size: 1em;
    margin-top: 5px;
    margin-left: 10px; }
  .post .post__underline {
    margin-left: 10px;
    height: 5px;
    width: 100px;
    background-color: #3470c9;
    margin-top: 5px; }
  .post .post__body {
    line-height: 1.7;
    margin-top: 5px;
    margin-left: 10px; }
  .post .post__readme {
    text-align: right;
    color: #3470c9;
    display: block;
    text-decoration: none; }
  .post .post__fullbody {
    line-height: 1.7; }
    .post .post__fullbody > * {
      margin-top: 20px; }
    .post .post__fullbody p {
      margin-top: 20px; }
      .post .post__fullbody p img {
        display: block;
        margin-left: auto;
        margin-right: auto; }
      .post .post__fullbody p img + strong {
        display: block;
        text-align: center; }
    .post .post__fullbody hr {
      border: solid 1px #3470c9; }
    .post .post__fullbody h1 {
      font-size: 1.5em;
      font-weight: 600;
      color: #3470c9; }
    .post .post__fullbody .highlight {
