      padding-left: 10px;
      padding-right: 10px; }
    .post .post__fullbody table tbody tr td {
      border: solid 1px #3470c9;
      padding-left: 10px;
      padding-right: 10px; }
    .post .post__fullbody table tbody tr:nth-of-type(odd) {
